<template>

  <print-pdf-layout>
    <pdf-header slot="header">發票</pdf-header>

    <pdf-invoice-member slot="membery"
      :the_member="membery"
      :_plus="false"
      :fs_type="`vw`"

      v-if="membery != null"
    ></pdf-invoice-member>

    <pdf-invoice-msg 
      slot="msg"
      v-if="membery!=null"

      :listing_payment="payment" 
      :listing_time="timed"
      :listing_num="numed"
      :member_num="membery.num" 
      :_plus="false"
      :fs_type="`vw`"
    >
    </pdf-invoice-msg>
    
    <pdf-invoice-freight
      v-if="listing_content.length > 0"

      :lising_content="listing_content"
      :fs_type="`vw`"

      slot="freights"
    ></pdf-invoice-freight>
    
    <pdf-footer :member="membery" v-if="membery!=null" slot="footer"
      :fs_type="`vw`"
    ></pdf-footer>
  
  </print-pdf-layout>

</template>

<script>
import conn from '@/service/conn'
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import config from '@/common/config'

import PdfHeader from '@/components/Pdf/PdfHeader.vue'
import PdfFooter from '@/components/Pdf/PdfFooter.vue'


import Loading from '@/components/Shimmer/Loading.vue'
import PrintPdfLayout from '@/components/Pdf/Print/PrintPdfLayout.vue'

import PdfInvoiceMember from '@/components/Pdf/Invoice/PdfInvoiceMember.vue'
import PdfInvoiceMsg from '@/components/Pdf/Invoice/PdfInvoiceMsg.vue'
import PdfInvoiceFreight from '@/components/Pdf/Invoice/PdfInvoiceFreight.vue'

export default {
  name: 'PrintPricesPdf',
  components: { 
    PdfFooter,
    PdfHeader,
    Loading,
    PrintPdfLayout,
    PdfInvoiceMember,
    PdfInvoiceMsg,
    PdfInvoiceFreight
  },
  data() {
    return {
      lc_id: 0,
      membery: null,
      listing: null,
      listing_content: [],

      timed: null,
      numed: null,
      payment: ''
    }
  },
  mounted() {
    const id = this.$route.query.lc_id
    
    conn.listing_content.getOneByListing({
      listing: id
    }).then( res => {
      
      if (conn.judge_response_list(res)) {
        res = res[0]
        this.listing = res.listing
        this.timed = tool.timed.ser_timed(this.listing.listing_time, true, true)
        this.numed = res.listing.num

        this.payment = view.ser_listing_paymet(res.listing.payment)

        this.membery = this.listing.membery
        this.listing_content = JSON.parse(res.content)
        
        setTimeout(() => {
          print()
        }, 800)
      }
      
    })
    
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


